:root {
	--main-green: #33ccc5;
	--main-blue: #4771cb;
	--main-transition: 0.5s;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	scroll-behavior: smooth;
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

a {
	text-decoration: none;
}

.container {
	padding-left: 15px;
	padding-right: 15px;
	margin-left: auto;
	margin-right: auto;
}

/* Small Screen */
@media (min-width: 768px) {
	.container {
		width: 750px;
	}
}

/* Medium Screen */
@media (min-width: 992px) {
	.container {
		width: 970px;
	}
}

/* Large Screen */
@media (min-width: 1200px) {
	.container {
		width: 1200px;
	}
}

.main-button {
	padding: 10px 20px;
	border-radius: 40px;
	border-bottom: 3px solid;
}

header {
	background-color: var(--main-green);
	position: relative;
}

header .container {
	max-width: 100%;
}

header .container nav {
	display: flex;
	align-items: center;
	min-height: 100px;
}

header .container nav .nav-logo {
	font-size: 30px;
	font-weight: 800;
	color: white;
}

header .container nav .nav-ul {
	flex: 1 1;
	display: flex;
	justify-content: flex-end;
	grid-gap: 25px;
	gap: 25px;
}

header .container nav .nav-ul .nav-link a {
	font-weight: 500;
	font-size: 18px;
	letter-spacing: 1px;
	color: #fff;
	text-transform: capitalize;
}

@media (max-width: 1199px) {
	header .container nav .nav-ul .nav-link a {
		font-size: 15px;
	}
}

@media (max-width: 767px) {
	header .container nav .nav-ul {
		display: none;
		position: absolute;
		bottom: -311px;
		left: 15%;
		background-color: #f7f7f7;
		flex-direction: column;
		width: 70%;
		align-items: center;
		grid-gap: 0;
		gap: 0;
	}

	header .container nav .nav-ul .nav-link {
		padding-top: 15px;
		padding-bottom: 15px;
		border-bottom: 1px solid #e7e7e7;
		width: 100%;
		text-align: center;
		transition: calc(var(--main-transition) / 2);
	}

	header .container nav .nav-ul .nav-link a {
		transition: calc(var(--main-transition) / 2);
		font-size: 18px;
		color: black;
	}

	header .container nav .nav-ul .nav-link:hover {
		background-color: #e7e7e7;
	}

	header .container nav .nav-ul .nav-link:hover a {
		color: var(--main-green);
	}
}

header .container nav .burger-icon {
	margin-left: auto;
	cursor: pointer;
}

@media (min-width: 767px) {
	header .container nav .burger-icon {
		display: none;
	}
}

header .container nav .burger-icon:hover + .nav-ul {
	display: flex;
}

header .container nav .burger-icon span {
	display: block;
	height: 2px;
	background-color: white;
}

header .container nav .burger-icon span:first-of-type,
header .container nav .burger-icon span:last-of-type {
	width: 22px;
}

header .container nav .burger-icon span:nth-of-type(2) {
	margin-top: 5px;
	margin-bottom: 5px;
	width: 30px;
}

header .container nav .nav-quote.main-button {
	font-weight: 500;
	font-size: 18px;
	letter-spacing: 1px;
	margin-left: 35px;
	color: white;
	background-color: var(--main-blue);
	transition: var(--main-transition);
}

header .container nav .nav-quote.main-button:hover {
	background-color: white;
	color: var(--main-blue);
	border-bottom-color: var(--main-blue);
}

@media (max-width: 1199px) {
	header .container nav .nav-quote.main-button {
		font-size: 15px;
	}
}

@media (max-width: 991px) {
	header .container nav .nav-quote.main-button {
		display: none;
	}
}

